* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  overflow-x: hidden;
}

header {
  background-color: #416d6d;
}

nav {
  width: 80%;
  max-width: 400px;
  margin: 0 auto;
}

nav ul {
  justify-content: space-between;
  list-style-type: none;
  display: flex;
}

nav li {
  margin: 20px 10px;
}

nav a {
  color: #000;
  padding: 20px 10px;
  font-size: 2rem;
  text-decoration: none;
  transition: all .3s;
}

nav a:hover {
  background: #3f7c7c;
}

main {
  flex-grow: 1;
}

.box {
  background-color: #d2f3f3;
  border: 1px solid #000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  width: 80%;
  max-width: 600px;
  margin: 100px auto;
  padding: 50px 20px;
  display: flex;
}

main h1, main h2 {
  text-align: center;
}

main h1 {
  font-size: 3.2rem;
}

main h2 {
  margin: 10px 0 20px;
  font-size: 2.8rem;
}

main p {
  font-size: 1.8rem;
  line-height: 1.4;
}

main picture {
  border: 1px solid #000;
  margin: 20px 0;
}

.work h1 {
  margin-bottom: 10px;
}

footer {
  background-color: #416d6d;
}

.footer {
  justify-content: space-between;
  width: 80%;
  max-width: 600px;
  margin: auto;
  padding: 50px;
  display: flex;
}

footer p, #clock {
  font-size: 1.8rem;
}

